* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input{
  font-size: 1.2rem;
  width: 100%;
  color: gray;
}

.card {
  align-content: center;
  border: 1px solid grey;
  overflow: hidden;
  margin-bottom: 10px;
  margin-bottom: 10px;
}

.card-grip{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card p {
  padding : 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}